import { resolveRef, safeFileExists, safeGetAllFilesPath, safeReadJson, safeWriteJson, trace } from '@msdyn365-commerce/utilities-internal';
import * as fs from 'fs-extra';
import * as path from 'path';
import { IModuleMetadata } from './models/IModuleMetadata';
import {
    DEFINITION,
    IModuleDefinition,
    localModuleDefaultDataPathPattern,
    THEME_MODULE_FILE_TYPE,
    THEME_MODULE_TYPE,
    themeModuleDefinitionExtensionsPathPattern,
    themeModuleLayoutsPathPattern,
    themeModuleSettingsPathPattern,
    themeModuleStylesPathPattern
} from './module-registration/module-registration-model';

const getModuleNameFromRegistration = (moduleRegistrationPath: string) => path.basename(moduleRegistrationPath, '.definition.json');

export interface ICopyOptions {
    fromPath: string;
    toPath: string;
}

/**
 * Gets all registered modules and some metadata about them
 */
export const getAllRegisteredModulePaths = async (): Promise<IModuleMetadata[]> => {
    return Promise.all([safeGetAllFilesPath(DEFINITION.fromLocalModule), safeGetAllFilesPath(DEFINITION.fromInstalledModule)]).then(
        ([localModules, installedModules]: [string[], string[]]) => {
            const modules = localModules.concat(installedModules);
            return Promise.all(
                modules.map(async (moduleRegistrationPath: string) => {
                    const containingFolder = path.dirname(moduleRegistrationPath);
                    const name = getModuleNameFromRegistration(moduleRegistrationPath);
                    return {
                        name,
                        containingFolder,
                        registrationPath: moduleRegistrationPath,
                        hasStyles: await safeFileExists(path.join(containingFolder, `${name}.scss`))
                    };
                })
            );
        }
    );
};

/**
 * Makes a copy of the .definition.json into /dist
 */
// tslint:disable-next-line:no-any
export async function copyDefinitionFilesIntoDist(params: ICopyOptions[]): Promise<any> {
    params.map(async param => {
        const connectorPackage = await safeGetAllFilesPath(path.join(process.cwd(), '**', 'connectors', '*', '*.connector.json'));
        const definitionFilePaths = await safeGetAllFilesPath(param.fromPath);
        if (!connectorPackage.length) {
            if (definitionFilePaths.length === 0) {
                trace.info(`No definitions found at '[${param.fromPath}]'`);
                return;
            }
        }

        try {
            return Promise.all(
                definitionFilePaths.map(async (filePath: string) => {
                    const pathToFileIfExists = await safeFileExists(filePath);
                    if (pathToFileIfExists) {
                        const definitionFileName = path.basename(filePath);
                        const moduleName = path.basename(path.dirname(filePath));
                        const outputPath = path.join(param.toPath, moduleName, definitionFileName);
                        // Resolve any refs and copy the definition json to the output path
                        await resolveRefsInDefinitionAndCopyToOutput(filePath, outputPath);
                    }
                })
            );
        } catch (e) {
            trace.error(e);
        }
    });
    // copy default data
    await copyModulePreviews(localModuleDefaultDataPathPattern);
}

/**
 * Uses the ref parser to resolve any refs in the module's definition json and copys the
 * resolved json to the output (build) path
 *
 * @param defJsonPath The path to the definition json which may or may not have refs
 * @param outputPath The path to the output folder where the resolved json with no refs will be written
 */
async function resolveRefsInDefinitionAndCopyToOutput(defJsonPath: string, outputPath: string): Promise<void> {
    let defJson = (await safeReadJson<IModuleDefinition>(defJsonPath)) || <IModuleDefinition>{};

    // ignore resolving $ref for themeModule as $ref is needed to figure the parent theme Module
    defJson = defJson.$type !== 'themeModule' ? await resolveRef(defJson, defJsonPath, trace) : defJson;

    await fs.ensureDir(path.dirname(outputPath));
    await safeWriteJson(outputPath, defJson);

    // copy theme related resources.
    if (defJson && defJson.$type === THEME_MODULE_TYPE) {
        await copyThemeModuleDefinitions(themeModuleDefinitionExtensionsPathPattern, THEME_MODULE_FILE_TYPE.DEFINITION_EXTENSIONS);
        await copyThemeModuleDefinitions(themeModuleLayoutsPathPattern, THEME_MODULE_FILE_TYPE.LAYOUTS);
        await copyThemeModuleDefinitions(themeModuleStylesPathPattern, THEME_MODULE_FILE_TYPE.STYLES);
        await copyThemeModuleDefinitions(themeModuleSettingsPathPattern, THEME_MODULE_FILE_TYPE.SETTINGS);
    }
}

/**
 * Uses the ref parser to resolve any refs in the module's definition json and copys the
 * resolved json to the output (build) path
 *
 * @param moduleFileGlogPattern The path to the preview data.
 */
async function copyModulePreviews(defaultPreviewDataFilePattern: string): Promise<void> {
    const defaultPreviewData = await safeGetAllFilesPath(defaultPreviewDataFilePattern);
    if (defaultPreviewData.length === 0) {
        trace.info(`No default data found with pattern ${defaultPreviewDataFilePattern}`);
    }

    try {
        await Promise.all(
            defaultPreviewData.map(async (filePath: string) => {
                const pathToFileIfExists = await safeFileExists(filePath);
                if (pathToFileIfExists && typeof pathToFileIfExists === 'string') {
                    const outputPath = pathToFileIfExists.replace('/src/modules', '/dist/lib/modules');
                    await fs.ensureDir(path.dirname(outputPath));
                    await fs.copyFile(pathToFileIfExists, outputPath);
                }
            })
        );
    } catch (e) {
        trace.error(`Error while copying default preview data. ${e}`);
    }
}

/**
 * Uses the ref parser to resolve any refs in the module's definition json and copys the
 * resolved json to the output (build) path
 *
 * @param defJsonPath The path to the definition json which may or may not have refs
 * @param outputPath The path to the output folder where the resolved json with no refs will be written
 */
async function copyThemeModuleDefinitions(moduleFileGlogPattern: string, fileType: string): Promise<void> {
    const themeModuleFilePaths = await safeGetAllFilesPath(moduleFileGlogPattern);
    if (themeModuleFilePaths.length === 0) {
        trace.info(`No theme ${fileType} files found`);
        return;
    }

    try {
        await Promise.all(
            themeModuleFilePaths.map(async (filePath: string) => {
                const pathToFileIfExists = await safeFileExists(filePath);
                if (pathToFileIfExists && typeof pathToFileIfExists === 'string') {
                    const outputPath = pathToFileIfExists.replace('/src/modules', '/dist/lib/modules');
                    await fs.ensureDir(path.dirname(outputPath));
                    await fs.copyFile(pathToFileIfExists, outputPath);
                }
            })
        );
    } catch (e) {
        trace.error(`Error while copying theme module definitions: ${e}`);
    }
}
